*,*:before,*:after{box-sizing:border-box}body{text-align:center}h1{font-weight:600;text-transform:uppercase}table{max-width:768px;margin:2em auto}td{border:1px solid #B6B6B6;width:20%;height:100px;vertical-align:middle;text-align:center;-webkit-transition:all 200ms linear;transition:all 200ms linear;height:0;padding:0;padding-bottom:20%;position:relative}td:hover{background:#90caf9;cursor:pointer;color:white}td.selected{background:#00BCD4;color:white}td .inner{position:absolute;top:0;right:0;bottom:0;left:0;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center}td .label{text-transform:uppercase}tr.won td,td.won{background:#000}#toast-container{top:10px;right:10px;max-width:300px}.won-card{position:absolute;top:0;right:0;bottom:0;left:0;z-index:10000;background:rgba(0,0,0,0.8);display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center}.won-card__inner{width:300px}



